import uniPopup from "@/components/uni-popup/uni-popup.vue"
import tkiQrcode from "@/components/tki-qrcode/tki-qrcode.vue"

export default {
	components: {
		uniPopup,
		tkiQrcode
	},
	data() {
		return {
			imgurl: "",
			relname: "",
			userID: "",
			agentname: "",
			nickname: "",
			sex: "",
			school: "",
			tel: "",
			company: "",
			onval: false, // val值变化时自动重新生成二维码
			loadMake: true, // 组件加载完成后自动生成二维码
		}
	},
	onLoad(options) {
		console.log(options);
		this.company = options.name
		this.tel = options.tel
		this.agentname = options.agentName
		
		this.__shop()
	},
	methods: {
		
		open() {
			this.$refs.popup.open()
			this.$refs.qrcode._makeCode()
		},
		async __shop() {
			this.$H.post("/user/", {
				user_id: "100003",
				token: "dXQyMDIwMDMyMDEwMjE0MTU0NjI2NTg1",
				app: "app"
			}).then((data) => {
				console.log(data);
				this.imgurl = data.headimgurl
				this.relname = data.user_info.realname
				this.userID = data.user_info.user_id
				this.nickname = data.user_info.nickname
				this.sex = data.user_info.sex
				this.school = data.user_info.school_id
			}).catch((data) => {
				console.log('--catch 错误！！');
			})

		}

	}
}
